.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;

  font-family: Arial, Helvetica, sans-serif;
  font-weight: 800;
}

body {
  background-color:rgb(225, 225, 225);
}

label {
  display: flex;
  justify-content: space-between;
  width: 400px;
}

input {
  width: 100px;
  border: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}
