.Carousel {
  width: 260px;
  margin: 0 auto;

  overflow: hidden;

  &__list {
    display: flex;
    list-style: none;
    padding: 0;

    transform: translateX(-260px);
  }

  &__button-place {
    display: flex;
    justify-content: space-between;
  }
}

button {
  background-color: aquamarine;
  border: transparent;

  width: 100px;
  height: 40px;

  font-weight: 700;
  font-size: 14px;

  cursor: pointer;

  transition: background-color 0.3s;

  &:hover {
    background-color: #fff;
  }

  &:disabled {
    background-color: #fff;
    cursor: auto;
  }
}
